import React from "react";
import PropTypes from "prop-types";
import { Button, Form, Message } from "semantic-ui-react";

const progLangsOptions = [
  { key: "r", text: "Ruby", value: "ruby" },
  { key: "e", text: "Elixir", value: "elixir" },
  { key: "g", text: "Go", value: "golang" },
  { key: "j", text: "JavaScript", value: "javascript" }
];

class GHReposForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchParams: {
        lang: "ruby",
        sort: "stars"
      }
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e, data = {}) {
    const name = e.target.name || data.name;
    const value = e.target.value != null ? e.target.value : data.value;
    const { searchParams } = this.state;
    switch (name) {
      case "lang":
        searchParams.lang = value;
        this.setState({ searchParams });
        break;
      case "sort":
        searchParams.sort = value;
        this.setState({ searchParams });
        break;
      default:
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    this.props.fetchRepos(this.state.searchParams);
  }

  render() {
    const loading = this.props.fetching;
    const currentSort = this.state.searchParams.sort;

    return (
      <div>
        <Message
          attached
          header="Popular GitHub repos"
          content={"Select the language and attribute you want to sort by"}
        />

        <Form
          action="#"
          method="get"
          onSubmit={this.handleSubmit}
          className="attached fluid segment"
          loading={loading}
        >
          <Form.Field width={8}>
            <label htmlFor="prog_lang">Programming language</label>

            <Form.Select
              id="prog_lang"
              name="lang"
              options={progLangsOptions}
              value={this.state.searchParams.lang}
              onChange={this.handleChange}
            />
          </Form.Field>

          <Form.Group inline>
            <label>Sort by number of: </label>
            <Form.Radio
              label="stars"
              name="sort"
              value="stars"
              checked={currentSort === "stars"}
              onChange={this.handleChange}
            />
            <Form.Radio
              label="forks"
              name="sort"
              value="forks"
              checked={currentSort === "forks"}
              onChange={this.handleChange}
            />
          </Form.Group>

          <Button type="submit">Search</Button>
        </Form>
      </div>
    );
  }
}

GHReposForm.propTypes = {
  fetchRepos: PropTypes.func.isRequired,
  fetching: PropTypes.bool.isRequired
};

export default GHReposForm;



