import React from "react";
import PropTypes from "prop-types";

class GHReposList extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showDetailsForRepoWithName: null };
  }

  static repoDetails(repo) {
    return (
      <ul>
        <li>
          <b>Owner: </b>
          {repo.owner}
        </li>
        <li>
          <b>Description: </b>
          {repo.desc}
        </li>
        <li>
          <b>URL: </b>
          {repo.url}
        </li>
        <li>
          <b>Time created: </b>
          {repo.created_at}
        </li>
        <li>
          <b>Time updated: </b>
          {repo.updated_at}
        </li>
        <li>
          <b>Number of starts: </b>
          {repo.stars_count}
        </li>
      </ul>
    );
  }

  toggleDetails(name) {
    let newVal = name;
    if (this.state.showDetailsForRepoWithName === name) {
      newVal = null;
    }
    this.setState({ showDetailsForRepoWithName: newVal });
  }

  listEl(repo) {
    return (
      <li key={repo.name}>
        <a
          href="#"
          onClick={e => {
            e.preventDefault();
            this.toggleDetails(repo.name);
          }}
        >
          {repo.name}
        </a>
        {this.state.showDetailsForRepoWithName === repo.name
          ? this.constructor.repoDetails(repo)
          : ""}
      </li>
    );
  }

  render() {
    return <ol>{this.props.repos.map(repo => this.listEl(repo))}</ol>;
  }
}

GHReposList.propTypes = {
  repos: PropTypes.array.isRequired
};

export default GHReposList;



