import React from "react";

import Form from "./GHReposForm";
import List from "./GHReposList";

class GHRepos extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      repos: [],
      fetching: false
    };
    this.fetchRepos = this.fetchRepos.bind(this);
  }

  fetchRepos(params) {
    if (!this.setState.fetching) {
      this.setState({ fetching: true });
    }
    fetch(`/repos/popular.json?lang=${params.lang}&sort=${params.sort}`)
      .then(response => response.json())
      .then(json => {
        if (json.repos === null) {
          setTimeout(() => {
            this.fetchRepos(params);
          }, 2000);
        } else {
          this.setState({ fetching: false });
          this.setState({ repos: json.repos });
        }
      });
  }

  render() {
    return (
      <div>
        <Form fetchRepos={this.fetchRepos} fetching={this.state.fetching} />
        <List repos={this.state.repos} />
      </div>
    );
  }
}

export default GHRepos;



